.card {
    display: flex;
    flex-direction: column;
    border: 1px solid black;
    border-radius: 0.25rem;
    width: 200px;
    flex-grow: 1;
    flex-shrink: 0;
    align-items: center;
    opacity: 0.6;
    padding: 1rem 0.5rem;
}

.card.checked {
    border-color: #d33333;
    opacity: 1;
}

.card:hover {
    opacity: 1;
}

.card.active {
    opacity: 1;
}

.card input[type=checkbox] {
    align-self: flex-start;
}

.card p {
    margin: 0 0 0.2rem 0;
    font-size: 0.9rem;
}

.card p.capital {
    text-transform: capitalize;
}

.card input[type=checkbox] {
    accent-color: #d33333;
    align-self: flex-start;
}