* {
  font-family: "Commissioner", sans-serif;
  font-size: 16px;
}

body {
  display: flex;
  justify-content: center;
}

body main {
  max-width: 1208px;
  display: flex;
  flex: 1;
  align-self: center;
}

#root {
  display: flex;
  flex: 1;
}

body section {
  flex: 1;
  margin-top: 2rem;
}

body section header {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  border-bottom: 3px solid black;
  padding-bottom: 1rem;
  margin-bottom: 1rem;
}

body section header .btn {
  background-color: #e5e5e5;
  padding: 0.5rem 1rem;
  border: 1px solid black;
  transition: all linear 0.2s;
  margin-right: 0.5rem;
}

body section header .btn:last-child {
  margin-right: 0;
}

body section header .btn:hover {
  cursor: pointer;
}

body section header .btn.btn-green:hover {
  background-color: #71a171;
  color: white;
}

body section header .btn.btn-red:hover {
  background-color: #d33333;
  color: white;
}

body section header a.btn {
  text-decoration: none;
  color: inherit;
}

body section header h1 {
  font-size: 2rem;
  font-weight: normal;
  margin: 0;
}

body section .error-message {
  border-radius: 0.5rem;
  max-width: 500px;
  background-color: #d33333;
  color: white;
  padding: 0.3rem 1rem;
}