label+span {
    color: #d33333;
    font-size: .85rem;
    text-align: right;
    margin-top: -1rem;
    margin-bottom: 1rem;
}

form {
    display: flex;
    flex-direction: column;
    margin-top: 2rem;
    width: 30rem;
}

form label {
    display: flex;
    position: relative;
    justify-content: space-between;
    align-items: baseline;
    margin-bottom: 1rem;
}

form label input,
form label select {
    width: 70%;
}

form label select {
    padding: 5px;
}